import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import filterFactory from "react-bootstrap-table2-filter";
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";

import DeleteModal from "../util/modal/DeleteModal";
import CustomerService from "../../services/CustomerService";

const { SearchBar } = Search;

class ListCustomerComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      customers: [],
      columns: [
        {
          dataField: "identityNumber",
          text: "MTER KODU",
          align: "center",
          title: true,
          sort: true,
        },
        {
          dataField: "name",
          text: "SM",
          align: "center",
          title: true,
          sort: true,
        },
        {
          dataField: "customerType",
          text: "MTER TP",
          align: "center",
          title: true,
          sort: true,
        },
        {
          dataField: "df",
          isDummyField: true,
          title: true,
          align: "center",
          headerStyle: () => {
            return { width: "30%" };
          },
          text: "LEMLER",
          formatter: (cellContent, row) => {
            let rowId = row.id;
            return (
              <div>
                <button
                  type="button"
                  style={{ marginRight: "10px" }}
                  onClick={() => this.view(rowId)}
                  className="btn btn-success"
                >
                  Grntle
                </button>
                <button
                  type="button"
                  style={{ marginRight: "10px" }}
                  onClick={() => this.update(rowId)}
                  className="btn btn-info"
                >
                  Gncelle
                </button>
                <DeleteModal
                  style={{ marginRight: "5px" }}
                  initialModalState={false}
                  data={row}
                  callback={this.delete}
                />
              </div>
            );
          },
        },
      ],
    };
    this.add = this.add.bind(this);
    this.view = this.view.bind(this);
    this.update = this.update.bind(this);
  }

  componentDidMount() {
    CustomerService.getAllCustomers()
      .then((res) => {
        this.setState({ customers: res.data });
      })
      .catch((ex) => {
        console.error(ex);
      });
  }

  add() {
    this.props.history.push("/add-customer/add/_add");
  }

  view(id) {
    this.props.history.push(`/add-customer/view/${id}`);
  }

  update(id) {
    this.props.history.push(`/add-customer/update/${id}`);
  }

  delete = (row) => {
    CustomerService.deleteCustomer(row.id)
      .then((res) => {
        CustomerService.getAllCustomers()
          .then((res) => {
            console.log(res.data);
            this.setState({ customers: res.data });
          })
          .catch((ex) => {
            console.error(ex);
          });
      })
      .catch((ex) => {
        console.error(ex);
      });
  };

  render() {
    const defaultSorted = [
      {
        dataField: "name",
        order: "asc",
      },
    ];

    const customTotal = (from, to, size) => (
      <span className="react-bootstrap-table-pagination-total">
        {from} - {to} aras gsteriliyor. Toplam Kayt Says: {size}
      </span>
    );

    const options = {
      paginationSize: 4,
      pageStartIndex: 1,
      firstPageText: "lk",
      prePageText: "nceki",
      nextPageText: "Sonraki",
      lastPageText: "Son",
      nextPageTitle: "lk Sayfa",
      prePageTitle: "Pre page",
      firstPageTitle: "lk Sayfa",
      lastPageTitle: "Son Sayfa",
      showTotal: true,
      paginationTotalRenderer: customTotal,
      disablePageTitle: true,
      sizePerPageList: [
        {
          text: "5",
          value: 5,
        },
        {
          text: "10",
          value: 10,
        },
        {
          text: "25",
          value: 25,
        },
        {
          text: "Tm",
          value: this.state.customers.length,
        },
      ],
    };

    return (
      <div className="container">
        <div className="col-sm-12 btn btn-info">Mteri Listesi</div>
        <div>
          <ToolkitProvider
            keyField="id"
            data={this.state.customers}
            columns={this.state.columns}
            bootstrap4="true"
            search
          >
            {(props) => (
              <div>
                <SearchBar {...props.searchProps} placeholder="Arama" />
                <button
                  type="button"
                  className="btn btn-primary addButton"
                  onClick={this.add}
                >
                  Mteri Ekle
                </button>
                <BootstrapTable
                  {...props.baseProps}
                  striped
                  hover
                  filter={filterFactory()}
                  pagination={paginationFactory(options)}
                  defaultSorted={defaultSorted}
                  noDataIndication="Veri bulunamad."
                />
              </div>
            )}
          </ToolkitProvider>
        </div>
      </div>
    );
  }
}

export default ListCustomerComponent;
